








































import { Component, Vue } from 'vue-property-decorator'
import UiButton from '@/components/UiButton.vue'
import UiInput from '@/components/Inputs/UiInput.vue'
import UiInputPassword from '@/components/Inputs/UiInputPassword.vue'
import { onLogin } from '../../../vue-apollo'
import { loginMutation } from '../../../api'
import { AuthData } from '../../../types';

@Component({
    components: { UiButton, UiInput, UiInputPassword },
})
export default class LoginForm extends Vue {
    email = ''
    password = ''
    errorText = ''

    login() {
        loginMutation({ email: this.email, password: this.password })
            .then(({ data }: { data: AuthData }) => {
                onLogin(this.$apollo.provider.defaultClient, data.login)
                this.$router.push('/tell-us')
            })
    }
}
